.card {
  padding-bottom: 95px;
}

.card__overlay {
  padding-top: 88px;
  padding-bottom: 103px;
  background-color: var(--basic-gray);
  background-image: url("../images/bg-card-overlay.png");
  background-repeat: no-repeat;
  background-position: left bottom;
}

.card__inner {
  display: flex;
  align-items: flex-start;
}

.card__thumbnail {
  flex-shrink: 0;
  margin-right: 40px;
}

.card__content {
  flex-grow: 1;
}

.card__title {
  margin-top: -2px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
}

.card__sku {
  margin-top: 10px;
  font-size: 17px;
  line-height: 20px;
  color: #5A6870;
}

.card__price {
  margin-top: 12px;
  margin-bottom: 28px;

  @include YesevaOne();

  font-size: 22px;
  line-height: 22px;
  text-transform: uppercase;

  .card__sku + & {
    margin-top: 34px;
  }
}

.card__buttons {
  display: flex;
  padding-bottom: 40px;
  margin-top: 80px;
  margin-bottom: 42px;
  border-bottom: 1px dashed #889D9A;

  .card__sku ~ & {
    margin-top: 0;
  }
}

.card__add-cart {
  display: flex;
  justify-content: center;
  width: 360px;
  padding-top: 24px;
  padding-bottom: 18px;
  letter-spacing: 1.3px;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-dark);
  border: 2px solid var(--basic-green);
  background-color: var(--basic-gray);
  box-sizing: border-box;
  @include Transition();

  &:hover {
    color: var(--basic-white);
    background-color: var(--basic-green);
    border-color: var(--basic-green);
  }

  &--custom {
    margin-left: 10px;
    border-color: #DBE7E5;
    background-color: #DBE7E5;
  }
}

.card__detail {
  position: relative;
  padding-left: 50px;
  letter-spacing: 0.2px;
  font-size: 17px;
  line-height: 24px;
  color: #5A6870;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 34px;
    height: 34px;
    background-color: var(--basic-white);
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
  }

  & p {
    margin-bottom: 6px;
  }

  &--about::before {
    background-image: url("../images/ico-check.svg");
  }

  &--delivery::before {
    top: 3px;
    background-image: url("../images/ico-car.svg");
  }

  &:not(:last-child) {
    margin-bottom: 32px;
  }

  & ul {
    @include List-Reset();
  }

  & a {
    color: var(--basic-green);
    text-decoration: none;
  }
}
