.card-tab {
  display: none;
  max-width: 890px;
  margin-top: 28px;
}

.card-tabs #tab1:checked ~ .card-tabs__content > #content-tab1,
.card-tabs #tab2:checked ~ .card-tabs__content > #content-tab2,
.card-tabs #tab3:checked ~ .card-tabs__content > #content-tab3,
.card-tabs #tab4:checked ~ .card-tabs__content > #content-tab4 {
  display: block;
}
