.complex {
  padding-top: 88px;
  padding-bottom: 100px;
  background-color: var(--basic-gray);
}

.complex__price {
  margin-top: 2px;
  margin-bottom: 29px;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 29px;
}

.complex__add-cart {
  display: block;
  width: 300px;
  padding-top: 18px;
  padding-bottom: 14px;
  margin-right: auto;
  margin-left: auto;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #1B262D;
  text-align: center;
  text-decoration: none;
  border: 2px solid #BDD1CC;
  box-sizing: border-box;
}
