.hint {
  position: absolute;
  top: 34px;
  left: 0;
  display: none;
  min-width: 360px;
  padding: 34px 55px 30px 20px;
  box-sizing: border-box;
  background-color: #F6F6F6;

  &::before {
    content: "";
    position: absolute;
    left: 47px;
    top: -7px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 18px 13px 18px;
    border-color: transparent transparent #f6f6f6 transparent;
  }

  .form-field--radio:checked ~ & {
    display: block;
  }
}

.hint__title {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: #5E7571;
}
