.delivery {
  width: calc(33.8% - 20px);
  padding: 33px 86px 35px 27px;
  box-sizing: border-box;
  border: 5px solid #DBE8E6;
}

.delivery__detail {
  @include List-Reset();

  font-size: 15px;
  line-height: 15px;
  color: #6D7B79;
}

.delivery__item {
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 14px;
  }

  &::before {
    content: "";
    display: block;
    flex-shrink: 0;
    width: 46px;
    height: 46px;
    margin-right: 29px;
    background-color: var(--basic-white);
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &--courier::before {
    background-image: url("../images/ico-delivery-1.svg");
  }

  &--mail::before {
    background-image: url("../images/ico-delivery-2.svg");
  }

  &--payment::before {
    background-image: url("../images/ico-delivery-3.svg");
  }
}

.delivery__more {
  display: inline-block;
  margin-top: 36px;
  margin-left: 75px;
  font-size: 15px;
  line-height: 15px;
  color: var(--basic-dark);
}
