.page-footer {
  color: #5A6870;
}

.page-footer__inner {
  display: flex;
  align-items: flex-start;
  padding-top: 76px;
  padding-bottom: 70px;
}

.page-footer__item {
  &:nth-child(1) {
    margin-right: 18%;
  }

  &:last-child {
    margin-left: auto;
  }
}

.page-footer__detail {
  margin-bottom: 26px;

  & p {
    margin-bottom: 6px;
    margin-top: 2px;
    font-size: 14px;
    line-height: 17px;
  }

  & a {
    text-decoration: none;
    color: inherit;
  }
}

.page-footer__more {
  padding: 14px 36px 10px;
  margin-top: 18px;
  display: inline-block;
  margin-left: auto;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 13px;
  line-height: 21px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-dark);
  border: 2px solid #F4F4F4;
  box-sizing: border-box;
  border-radius: 61px;
  @include Transition();

  &:hover {
    color: var(--basic-green);
    border-color: var(--basic-green);
  }
}
