.faq {
  margin-top: 100px;
  padding-bottom: 62px;
}

.faq__person {
  display: block;
  margin-right: auto;
  margin-left: auto;
  border-radius: 50%;
  border: 5px solid var(--basic-green);
}

.faq__title {
  margin-top: 27px;
  margin-bottom: 0;

  @include YesevaOne();

  font-size: 34px;
  line-height: 41px;
  text-align: center;
}

.faq__sub-title {
  margin-top: 12px;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  color: var(--basic-green);
}

.faq__list {
  margin: 74px 110px 0;
}
