.history-brand {
  position: relative;
  margin-top: 88px;
  padding-top: 49px;
  padding-left: 80px;
  padding-bottom: 69px;

  @media (max-width: $template-desktop) {
    margin-left: 220px;
    padding-left: 100px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: calc(100vw - ((100vw - 1420px) / 2));
    background-color: #EFF5F4;
    background-image: url("../images/bg-about-brand.png");
    background-position: right bottom;
    background-repeat: no-repeat;

    @media (max-width: $template-desktop) {
      background-position: -180px 140px;
    }
  }

  & p {
    margin-bottom: 14px;
    margin-top: 14px;
    font-size: 17px;
    line-height: 27px;

    &:not(:last-child) {
      max-width: 770px;
    }

    &:last-child {
      margin-top: 68px;
      font-weight: 300;
      font-size: 32px;
      line-height: 42px;

      & span {
        color: var(--basic-green);
      }
    }
  }
}

.history-brand__inner {
  position: relative;
  max-width: 810px;
}

.history-brand__title {
  margin-bottom: 41px;

  @include YesevaOne();

  font-size: 34px;
  line-height: 41px;
}


