.cart-item {
  &:not(:last-child) {
    & td {
      border-bottom: 1px dashed #9AADA5;
    }
  }

  & td {
    padding: 20px 0 10px 20px;

    &:last-child {
      padding-right: 20px;
    }
  }
}

.cart-item__title {
  font-size: 18px;
  line-height: 25px;
  text-decoration: none;
  color: #1B262D;
}

.cart-item__title-wrapper {
  max-width: 374px;
}

.cart-item__price {
  font-size: 18px;
  line-height: 25px;
}

.cart-item__remove {
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  transform: rotate(45deg) scale(0.8);

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: #C0C0C0;
  }

  &::before {
    top: 10px;
    left: 0;
    width: 22px;
    height: 2px;
  }

  &::after {
    top: 0;
    left: 10px;
    width: 2px;
    height: 22px;
  }
}
