.reviews-slider__item {
  display: flex !important;
  flex-wrap: wrap;
  align-items: center;
  border-radius: 9px;
  background-color: var(--basic-white);
  box-sizing: border-box;

  &--text {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 17px;
      top: -13px;
      width: 30px;
      height: 24px;
      background-image: url("../images/ico-quote.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  & p {
    margin-right: 17px;
    margin-left: 17px;
    font-size: 16px;
    line-height: 22px;
  }
}

.reviews-slider__thumbnail {
  border-radius: 9px;
}
