.cart {
  position: relative;
  z-index: 1;
  width: 100%;
  border-collapse: collapse;
  background-color: var(--basic-white);
}

.cart__overlay {
  padding-top: 60px;
  padding-bottom: 100px;
  background-color: var(--basic-gray);
  background-image: url("../images/bg-card-overlay.png");
  background-repeat: no-repeat;
  background-position: left bottom;
}

.cart__grid {
  display: flex;
  align-items: flex-start;
}

.cart__wrapper {
  display: flex;
  flex-direction: column;
  width: 66.2%;
  flex-shrink: 0;
  margin-right: 20px;
}

.cart__total-price {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 240px;
  height: 100px;
  margin-top: 1px;
  margin-bottom: 29px;
  margin-left: auto;
  font-weight: 700;
  font-size: 22px;
  line-height: 31px;
  text-transform: uppercase;
  color: var(--basic-green);
  background-image: url("../images/bg-total-price.png");
  background-repeat: repeat-x;
  background-position: top center;
  filter: drop-shadow(0 13px 16px #D9E8E5);
}

.cart__place-order {
  width: 240px;
  padding-top: 23px;
  padding-bottom: 19px;
  margin-left: auto;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-dark);
  background-color: var(--basic-gray);
  border: 2px solid var(--basic-green);
  box-sizing: border-box;

  &--disabled {
    border-color: #DBE8E5;
    color: #94AAA6;
  }
}
