.counter {
  display: flex;
  align-items: center;
  width: 110px;
  height: 40px;
  border: 1px solid #E3E3E3;
  box-sizing: border-box;
}

.counter__control {
  position: relative;
  display: block;
  width: 30px;
  height: 38px;
  flex-shrink: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  box-sizing: border-box;
  cursor: pointer;

  &--minus {
    border-right: 1px solid #E3E3E3;
  }

  &--plus {
    border-left: 1px solid #E3E3E3;

    &::after {
      content: "";
      position: absolute;
      left: 14px;
      top: 13px;
      width: 1px;
      height: 13px;
      background-color: #69A735;
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 8px;
    top: 19px;
    width: 13px;
    height: 1px;
    background-color: #69A735;
  }
}

.counter__input {
  display: block;
  width: calc(100% - 60px);
  padding: 0;
  border: 0;
  text-align: center;
  font-size: 18px;
  line-height: 25px;
  color: #000000;
}

