.catalog {
  @include List-Reset();

  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 20px);
  margin-left: -10px;
  margin-right: -10px;
}

.catalog__more-link {
  position: relative;
  padding: 21px 32px 17px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.5px;
  line-height: 22px;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--basic-dark);
  background-color: var(--basic-white);
  @include Transition();

  &:hover {
    color: var(--basic-white);
    background-color: var(--basic-green);
  }

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    border: 1px solid #B2D059;
  }
}

.catalog__more {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(50% - 20px);
  margin: 10px;
  background-color: #F0F5F4;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-image: url("../images/bg-catalog-more.jpg");
    mix-blend-mode: multiply;
    background-position: left bottom;
    background-repeat: no-repeat;
    opacity: 0.05;
  }
}
