.form-field {
  display: block;
  width: 100%;
  padding: 15px 20px 13px;
  font-size: 16px;
  line-height: 16px;
  color: var(--basic-black);
  border: 2px solid #EDEDED;
  box-sizing: border-box;
  @include Transition();

  &:focus {
    border-color: var(--basic-green);
  }

  &::placeholder {
    font-size: 16px;
    line-height: 16px;
    font-family: inherit;
    color: #6F6F6F;
    opacity: 1;
  }

  &--radio {
    display: none;
  }

  &--textarea {
    max-width: 1060px;
    height: 150px;
    resize: none;
  }
}

.form-field__wrapper {
  display: block;
  width: 280px;
  margin-right: 20px;

  .order-item--delivery-method & {
    position: relative;
    width: auto;
    margin-right: 54px;
  }

  .order-item--payment-method & {
    width: auto;
    margin-right: 50px;
  }

  .order-item--comment & {
    width: 100%;
    margin-right: 0;
  }

  .place-detail & {
    width: 140px;
  }
}

.form-field__title {
  display: flex;
  align-items: center;
  letter-spacing: 0.4px;

  &::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: var(--basic-white);
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
  }

  & a {
    margin-left: 6px;
    color: var(--basic-green)
  }

  .order-item--delivery-method & {
    font-size: 16px;
    line-height: 16px;
    color: #6F6F6F;
  }

  .form-field--radio:checked ~ & {
    color: var(--basic-black);

    &::before {
      border-color: var(--basic-green);
      border-width: 4px;
    }
  }
}
