.not-found__description {
  margin-top: 70px;
  margin-bottom: 31px;
  font-size: 24px;
  line-height: 29px;
  color: var(--basic-black);
}

.not-found__home {
  width: 220px;
  padding-top: 19px;
  padding-bottom: 15px;
  font-weight: 500;
  text-align: center;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1.3px;
  color: var(--basic-dark);
  text-decoration: none;
  text-transform: uppercase;
  border: 2px solid var(--basic-green);
  box-sizing: border-box;
}
