.header-cart {
  position: relative;
  margin-left: auto;
  margin-top: 4px;
  margin-right: 12px;
  text-decoration: none;
}

.header-cart__ico {
  fill: var(--basic-dark);
  @include Transition();

  .header-cart:hover & {
    fill: var(--basic-green);
  }
}

.header-cart__counter {
  position: absolute;
  right: -12px;
  top: -12px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: var(--basic-dark);
  background-color: var(--basic-white);
  border-radius: 50%;

  body:not(.home) & {
    background-color: var(--basic-gray);
  }
}
