.about {
  padding-top: 86px;
  padding-bottom: 100px;
}

.about__list {
  display: flex;
}

.about__more {
  padding: 19px 41px 15px;
  margin-top: 48px;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  text-decoration: none;
  text-transform: uppercase;
  color: #1B262D;
  border: 2px solid #DFDFDF;
  @include Transition();

  &:hover {
    color: var(--basic-green);
    border-color: var(--basic-green);
  }
}
