.order-footer {
  padding-bottom: 40px;
  background-color: var(--basic-gray);
}

.order-footer__consent {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 15px;
  color: var(--basic-black);

  & a {
    margin-left: 6px;
    color: var(--basic-green);
    text-decoration: none;
  }

  & #consent {
    display: none;
  }
}

.order-footer__consent-text {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  letter-spacing: 0.3px;

  &::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    margin-right: 9px;
    background-color: var(--basic-green);

    #consent:checked ~ & {
      background-image: url("../images/ico-check-small.svg");
      background-repeat: repeat-x;
      background-position: top center;
    }
  }
}
