.slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 0;
  padding-left: 0;
  border: 0;
  cursor: pointer;
  @include Transition();

  &:hover {
    border-color: var(--basic-green);

    & svg {
      fill: var(--basic-green);
    }
  }

  .reviews & {
    width: 40px;
    height: 40px;
    margin-top: -20px;
    border-radius: 50%;
    background-color: transparent;
    border: 1px solid #CECECE;

    &:hover {
      border-color: var(--basic-green);

      & svg {
        fill: var(--basic-green);
      }
    }
  }

  .reviews &.slick-prev {
    left: -65px;

    @media (max-width: $template-desktop) {
      left: -60px;
    }
  }

  .reviews &.slick-next {
    right: -65px;

    @media (max-width: $template-desktop) {
      right: -60px;
    }
  }

  & svg {
    fill: #4B4B4B;
    @include Transition();
  }
}
