.faq-item {
  padding-bottom: 8px;

  &:not(:last-child) {
    border-bottom: 1px dashed #9A9A9A;
  }
}

.faq-item__title {
  margin-top: 21px;
  margin-bottom: 16px;
  font-style: italic;
  font-weight: 500;
  font-size: 19px;
  line-height: 23px;
}

.faq-item__detail {
  position: relative;
  margin-top: 0;
  padding-left: 35px;
  font-size: 17px;
  line-height: 24px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 2px;
    width: 21px;
    height: 18px;
    background-image: url("../images/ico-faq.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}
