.product {
  display: flex;
  flex-direction: column;
  width: calc(25% - 20px);
  margin: 10px;
  padding: 20px;
  background-color: var(--basic-white);
  box-sizing: border-box;
  @include Transition();

  &:hover {
    box-shadow: 0 4px 24px #CFDCD8;
  }
}

.product__thumbnail-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 243px;
  margin-top: 17px;
  margin-bottom: 8px;
}

.product__stage {
  letter-spacing: 1px;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #A5C5BC;
}

.product__title {
  margin-top: 12px;
  margin-bottom: 10px;
  font-size: 17px;
  text-decoration: none;
  line-height: 20px;
  text-align: center;
  color: var(--basic-black);

  &:hover {
    color: var(--basic-green);
  }
}

.product__active {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.product__price {
  margin-top: 10px;
  margin-bottom: 0;

  @include YesevaOne();

  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--basic-green);
}

.product__add-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--basic-light-gray);
  border-radius: 50%;
  @include Transition();

  & svg {
    width: 20px;
    height: 18px;
    fill: var(--basic-dark);
    @include Transition();
  }

  &:hover {
    background-color: var(--basic-green);

    & svg {
      fill: var(--basic-white);
    }
  }
}
