:root {
  --basic-dark: #1B262D;
  --basic-green: #93BA1F;
  --basic-gray: #EBF1F0;
  --basic-light-gray: #E5ECEA;
  --basic-white: #ffffff;
  --basic-black: #000000;
}

/* Scroll Bar */
* {
  outline: none;
  -webkit-tap-highlight-color: transparent;
  scrollbar-color: var(--basic-green) transparent;
  scrollbar-width: thin;
}
*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-thumb {
  background-color: #2aa8c6;
  border-radius: 4px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
  width: 2px;
}

html {
  font-smoothing: antialiased;
}

body {
  min-width: $extra-small;
  @include Muller();
  font-size: 16px;
  line-height: 1.7;
  color: var(--basic-dark);
  background-color: var(--basic-white);
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: var(--basic-green);
  @include Transition(text-decoration, .2s);

  &:hover {
    text-decoration: none;
  }
}

