.main-menu {
  @include List-Reset();

  display: flex;

  & li {
    padding-right: 40px;

    &:not(:last-child) {
      position: relative;
      margin-right: 32px;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 10px;
        width: 8px;
        height: 8px;
        background-color: #C5D0CD;
        border-radius: 50%;
      }
    }
  }

  & a {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    letter-spacing: 1.1px;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--basic-dark);
    @include Transition();

    &:hover {
      color: var(--basic-green);
    }
  }
}
