.stages-detail {
  @include List-Reset(47px, 0, 0);

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 90px;
  margin-right: 90px;
}

.stages-detail__item {
  width: calc(50% - 50px);

  &:nth-child(1) {
    order: 1;
  }

  &:nth-child(2) {
    order: 3;
  }

  &:nth-child(3) {
    order: 2;
  }

  &:nth-child(4) {
    order: 4;
  }
}

.stages-detail__step {
  margin-bottom: 0;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;
  text-transform: uppercase;
  color: var(--basic-green);
}

.stages-detail__title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  letter-spacing: 0.7px;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}

.stages-detail__description {
  margin-top: 13px;
  letter-spacing: 0.3px;
  font-size: 17px;
  line-height: 24px;
  color: var(--basic-dark);
}
