.section-title {
  @include YesevaOne();

  margin-top: 20px;
  margin-bottom: 60px;
  font-size: 34px;
  line-height: 41px;
  text-align: center;

  .about & {
    margin-top: 9px;
  }

  .article & {
    margin-bottom: 57px;
  }

  .scheme & {
    max-width: 900px;
    margin-top: 8px;
  }

  .reviews & {
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 43px;
  }

  .complex & {
    margin-bottom: 45px;
  }
}

.section-title__sub {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  line-height: 13px;
  font-weight: 700;
  letter-spacing: 2.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--basic-green);

  &::before,
  &::after {
    content: "";
    display: block;
    width: 18px;
    height: 1px;
    background-color: var(--basic-green);
  }

  &::before {
    margin-right: 8px;
  }

  &::after {
    margin-left: 8px;
  }
}
