.about-item {
  width: calc((100% - 40px) / 3);
}

.about-item__title {
  min-height: 48px;
  max-width: 300px;
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--basic-green);
}

.about-item__description {
  @include List-Reset();

  font-size: 17px;
  line-height: 17px;

  & li {
    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }
}
