.page-header {
  position: relative;

  .home & {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: 10;
  }

  body:not(.home) & {
    background-color: var(--basic-white);
    box-shadow: 0 4px 37px #D2DFDD;
  }
}

.page-header__inner {
  display: flex;
  align-items: flex-start;
  padding-top: 28px;
  padding-bottom: 10px;
  border-bottom: 4px solid var(--basic-white);

  body:not(.home) & {
    border-bottom: 2px solid transparent;
  }
}
