.order-total-price {
  @include List-Reset(0, 31px, 0);

  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  width: 240px;
  height: 100px;
  padding-right: 13px;
  padding-left: 13px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 700;
  font-size: 22px;
  line-height: 31px;
  text-transform: uppercase;
  color: var(--basic-green);
  background-image: url("../images/bg-total-price.png");
  background-repeat: repeat-x;
  background-position: top center;
  filter: drop-shadow(0 13px 16px #D9E8E5);
  box-sizing: border-box;
}

.order-total-price__item {
  display: flex;
  align-items: center;
}

.order-total-price__detail {
  width: 113px;
  text-transform: lowercase;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #6F6F6F;
}
