.socials {
  @include List-Reset(7px, 0, 0);

  display: flex;
  justify-content: flex-end;
}

.socials__item {
  margin-left: 10px;

  & a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: #F4F4F4;
    border-radius: 50%;

    & svg {
      fill: var(--basic-dark);
      @include Transition();
    }

    &:hover {
      & svg {
        fill: var(--basic-green);
      }
    }
  }
}
