.components-item {
  padding-left: 80px;
  margin-bottom: 38px;
}

.components-item__title {
  display: flex;
  align-items: center;
  margin-bottom: 2px;
  margin-left: -80px;
  font-weight: 500;
  font-size: 22px;
  line-height: 31px;
  color: var(--basic-green);

  &::before {
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    margin-right: 30px;
    background-color: var(--basic-white);
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    box-sizing: border-box;
  }

  .components-item--essential-oils &::before {
    background-image: url("../images/ico-essential-oils.svg");
  }

  .components-item--natural-oil &::before {
    background-image: url("../images/ico-natural-oil.svg");
  }

  .components-item--vitamins &::before {
    background-image: url("../images/ico-vitamins.svg");
    border: 1px solid var(--basic-green);
  }

  .components-item--extracts &::before {
    background-image: url("../images/ico-extracts.svg");
  }

  .components-item--proteins &::before {
    background-image: url("../images/ico-proteins.svg");
  }
}

.components-item__detail {
  max-width: 860px;
  margin-top: 0;
  padding-top: 39px;
  border-top: 3px solid #D8E2E1;

  & dt {
    font-size: 20px;
    line-height: 28px;
  }

  & dd {
    margin-left: 0;
    margin-top: 9px;
    margin-bottom: 26px;
    font-size: 17px;
    line-height: 24px;
    color: #4B5962;
  }
}
