.stages-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: calc((100% - 130px * 3) / 4);
  padding-bottom: 40px;
  padding-right: 41px;
  padding-left: 29px;
  background-image: url("../images/bg-stage-item.png");
  background-repeat: no-repeat;
  background-position: -28px 21px;
  box-sizing: border-box;

  &:not(:last-child)::after {
    content: "";
    position: absolute;
    right: -75px;
    top: 125px;
    width: 20px;
    height: 33px;
    background-image: url("../images/ico-right-arrow.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}

.stages-item__step {
  position: absolute;
  top: 35px;
  right: -17px;
  padding: 11px 15px;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--basic-green);
  background-color: var(--basic-white);
  border-radius: 20px;

  .stages-item:nth-child(1) &,
  .stages-item:nth-child(2) & {
    right: -40px;
  }
}
