.card-tabs {
  margin-top: 94px;

  & input {
    display: none;
  }
}

.card-tabs__item {
  position: relative;
  margin-right: 99px;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 20px;
  cursor: pointer;
  @include Transition();

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -16px;
    width: 100%;
    height: 2px;
    background-color: var(--basic-green);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  }

  &--youtube {
    &::before {
      content: "";
      display: inline-block;
      vertical-align: text-top;
      width: 25px;
      height: 17px;
      margin-right: 7px;
      background-image: url("../images/ico-youtube.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  .card-tabs #tab1:checked + &,
  .card-tabs #tab2:checked + &,
  .card-tabs #tab3:checked + &,
  .card-tabs #tab4:checked + & {
    color: var(--basic-green);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -16px;
      width: 100%;
      height: 2px;
      background-color: var(--basic-green);
      transform: scaleX(1);
    }
  }

  &:hover {
    color: var(--basic-green);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -16px;
      width: 100%;
      height: 2px;
      background-color: var(--basic-green);
      transform: scaleX(1);
      transform-origin: left;
      transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
    }
  }
}

.card-tabs__content {
  margin-top: 9px;
  border-top: 2px solid #EBF1F0;

  & h2 {
    margin-top: 45px;
    margin-bottom: 24px;
    font-style: italic;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: var(--basic-green);

    & ~ h2 {
      margin-top: 50px;
    }
  }

  & p {
    margin-bottom: 16px;
    margin-top: 16px;
    letter-spacing: 0.2px;
    font-size: 18px;
    line-height: 27px;
  }
}
