.modal {
  display: none;
  position: relative;
  width: 680px;
  height: 310px;
  background-color: var(--basic-white);
  box-sizing: border-box;

  &.show {
    display: block;
  }

  &--success {
    padding-left: 200px;
    border-radius: 7px;
    background-image: url("../images/bg-modal-order.jpg");
    background-repeat: no-repeat;
    background-position: left bottom;
  }

  &--map {
    &.show {
      display: flex;
      width: 1300px;
      height: 500px;
    }
  }
}

.modal__overlay {
  display: none;

  &.show {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    background-color: rgba(75, 89, 98, 0.35);
  }
}

.modal__content {
  &--success {
    padding: 70px 70px 80px 50px;

    & p {
      margin-top: 0;
      margin-bottom: 15px;
      font-size: 20px;
      line-height: 24px;
      color: var(--basic-black);
    }
  }

  &--order {
    padding: 47px 90px 60px 32px;
  }
}

.modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  padding-left: 0;
  padding-right: 0;
  background-color: transparent;
  border: 0;
  z-index: 20;
  cursor: pointer;
}

.modal__close-icon {
  position: relative;
  display: block;
  width: 21px;
  height: 21px;
  box-sizing: border-box;
  border: 1px solid var(--basic-dark);
  border-radius: 50%;
  transform: rotate(45deg);
  @include Transition();

  &:hover {
    border-color: var(--basic-green);

    &::before,
    &::after {
      background-color: var(--basic-green);
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: var(--basic-dark);
  }

  &::before {
    top: 9px;
    left: 3px;
    width: 13px;
    height: 1px;
  }

  &::after {
    top: 3px;
    left: 9px;
    width: 1px;
    height: 13px;
  }
}

.modal__active {
  margin-top: 23px;

  .modal__content--order & {
    margin-top: 40px;
  }
}

.modal__button {
  display: inline-block;
  width: 200px;
  padding-top: 13px;
  padding-bottom: 10px;
  font-weight: 500;
  text-align: center;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.3px;
  color: var(--basic-dark);
  text-decoration: none;
  text-transform: uppercase;
  border: 2px solid #DFDFDF;
  box-sizing: border-box;
  @include Transition();

  &:hover {
    color: var(--basic-white);
    background-color: var(--basic-green);
    border-color: var(--basic-green);
  }

  &--custom {
    display: none;
    border-color: #EBEBEB;
    background-color: #EBEBEB;
  }
}
