.order-item {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-bottom: 59px;

  &:not(:last-of-type) {
    margin-bottom: 35px;
    border-bottom: 1px dashed #A7A7A7;
  }

  &--delivery-method {
    padding-bottom: 214px;
  }
}

.order-item__title {
  width: 100%;
  margin-top: 23px;
  margin-bottom: 31px;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--basic-green);
}
