.promo {
  position: relative;
  height: 1000px;
  background-color: var(--basic-light-gray);
  background-image: url("../images/bg-promo-decoration.png");
  background-position: 30px 100%;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    width: 67px;
    height: 1008px;
    background-image: url("../images/bg-promo-figure.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.promo__girl {
  position: absolute;
  bottom: 0;
  right: 0;

  @media (max-width: $template-desktop) {
    right: -140px;
  }
}

.promo__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.promo__decoration {
  position: absolute;
  left: 0;
  top: 220px;

  @media (max-width: $template-desktop) {
    left: -10px;
  }
}

.promo__text {
  margin-top: 220px;
}

.promo__description {
  max-width: 620px;
  margin-bottom: 44px;
  margin-top: 33px;
  font-size: 44px;
  line-height: 44px;
  font-weight: 100;
  text-align: center;
}

.promo__thumbnail {
  filter: drop-shadow(-41px 35px 49px rgba(50, 85, 77, 0.21));
}
