/* Muller Ultra Light */
@font-face {
  font-family: "Muller";
  src: url("../fonts/muller-ultra-light.woff2") format("woff2"),
  url("../fonts/muller-ultra-light.woff") format("woff");
  font-style: normal;
  font-weight: 100;
  font-display: swap;
}

